import * as React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"

const ProductsPage = ({ data }) => {
  const allProducts = data.allMarkdownRemark.nodes
  const getImageFromGatsby = image => getImage(image)
  return (
    <Layout>
      <Seo title="Products" />
      {/* <h1>Hi people</h1>
    <p>Welcome to your new Gatsby site.</p>
    <p>Now go build something great.</p>
    <StaticImage
      src="../images/gatsby-astronaut.png"
      width={300}
      quality={95}
      formats={["auto", "webp", "avif"]}
      alt="A Gatsby astronaut"
      style={{ marginBottom: `1.45rem` }}
    />
    <p>
      <Link to="/page-2/">Go to page 2</Link> <br />
      <Link to="/using-typescript/">Go to "Using TypeScript"</Link>
    </p> */}
      {allProducts &&
        allProducts.map(product => (
          <section className="page-section">
            <div className="container">
              <div className="product-item">
                <div className="product-item-title d-flex">
                  <div className="bg-faded p-5 d-flex ms-auto rounded">
                    <h2 className="section-heading mb-0">
                      <span className="section-heading-upper">
                        {product.frontmatter.subtitle}
                      </span>
                      <span className="section-heading-lower">
                        {product.frontmatter.title}
                      </span>
                    </h2>
                  </div>
                </div>
                <GatsbyImage
                  className="product-item-img mx-auto d-flex rounded img-fluid mb-3 mb-lg-0 custom-box-shadow"
                  image={getImageFromGatsby(product.frontmatter.headerImage)}
                  quality={95}
                  width={500}
                  placeholder="blurred"
                  formats={["auto", "webp", "avif"]}
                  alt="sun"
                />
                <div className="product-item-description d-flex me-auto">
                  <div className="bg-faded p-5 rounded">
                    <p className="mb-0">
                      {product.frontmatter.productDescription}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </section>
        ))}
    </Layout>
  )
}

export default ProductsPage

export const pageQuery = graphql`
  query AllProducts {
    allMarkdownRemark(
      filter: { frontmatter: { type: { eq: "data" }, for: { eq: "products" } } }
    ) {
      nodes {
        frontmatter {
          title
          for
          description
          productDescription
          subtitle
          headerImage {
            childImageSharp {
              gatsbyImageData
            }
          }
        }
      }
    }
  }
`
